.rating__rating-reviews {
  font-size: 10px;
  line-height: 14px;
  color: var(--dark-blue-color);
  text-decoration: none;
}

.rating__stars {
  display: flex;
}

.rating__star {
  display: flex;
  background-image: url(../images/star.svg);
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  background-position: center;
}

.rating__stars--1 .rating__star:nth-child(-n+1),
.rating__stars--2 .rating__star:nth-child(-n+2),
.rating__stars--3 .rating__star:nth-child(-n+3),
.rating__stars--4 .rating__star:nth-child(-n+4),
.rating__stars--5 .rating__star:nth-child(-n+5) {
  background-image: url(../images/star-active.svg);
}
